
@media (max-width: 768px) {
  .picture {
    width: 100% !important;
  }
  .img {
    max-width: 100% !important;
  }
  footer {
    height: 40px !important;
  }
  footer p {
    font-size: 15px !important;
  }
}


* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

p {
  margin: 0 !important;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.blockMenu {  
  top: 33%;
  /* margin-top: -50%;
  transform: translate(-50%, -50%); */
  font-family: 'Archivo Narrow', sans-serif;
  display: flex;
  /* justify-content: center; */
  /* align-items: center; */
  flex-direction: column;
  text-align: center;
  position: fixed;
}

.main {
  display: flex;
  /* align-items: center; */
  flex-direction: row;
  justify-content: space-between;
}

.group {
  text-decoration: none;
  outline: none;
  list-style-type: none;
  padding: 0;
  color: gray;
  font-size: 18px;
}

.group li:hover{
  cursor: pointer;
  color: black;
}

.group li:first-child {
  font-weight: bold;
  font-size: 20px;
}

.site-logo a, .site-logo{
  text-decoration: none !important;
  outline: none !important;
  font-family: 'Gayathri', sans-serif;
  font-size: 22px;
  font-weight: bold;  
  color: black;
  cursor: pointer;
  margin: 0;
  padding: 0;
}

.site-logo a:hover, nav div p:hover {
  color: grey;
}

nav div p{
  font-size: 18px;
  cursor: pointer;
}

.picture .img img {
  max-width: 100%;
  height: auto;
} 
.img {
  max-width: 50%;
  height: auto;
  position: relative;
  cursor: pointer;
}
/* 1727 */

.menu {
  display: flex;
  width: 30%;
  justify-content: center;
}

.picture {
  width: 70%;
  display: flex;
  flex-wrap: wrap;
}

.first:hover .BlockImg0, .second:hover .BlockImg1, .third:hover .BlockImg2, .fourth:hover .BlockImg3 {
  display: flex;
}

.BlockImg0, .BlockImg1, .BlockImg2, .BlockImg3 {
  display: none;
  background: rgba(255, 255, 255, 0.924);
  position: absolute;
  width: 90%;
  height: 90%;
  margin: 5% 0 0 5%;
  justify-content: center;
  align-items: center;
}

.BlockImg0 p, .BlockImg1 p, .BlockImg2 p, .BlockImg3 p {
  font-family: 'Gayathri', sans-serif;
  font-size: 30px;
  font-weight: 350;
}

footer {
  height: 100px;
  text-align: center;
  width: 100%;
  display: flex !important;
  justify-content: center;
  align-items: center;
}

footer p {
  font-family: 'Dancing Script', regular;
  margin: 0;
  font-size: 30px;
}

.header_mobile {
  position: fixed;
  top: 0;
  background: white;
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}